.header {
  padding: 15px 0;
  background-color: #ffffff;
  .logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 80%;
  }
  .emp-text {
    font: normal normal bold 20px/10px Montserrat;
    font-family: "Montserrat", sans-serif;
    letter-spacing: 0px;
    color: #82a70c;
    text-transform: capitalize;
  }
  .emp-payroll {
    color: #42515f;
  }
}
.payroll-main {
  height: 100%;
  .content {
    min-height: calc(100% - 80px);
    background-color: #f7f7f7;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    .form {
      /* padding: 35px 10%; */
      /* width: 80%; */
      width: 65%;
      margin: 20px 0px 20px 0px;
      padding: 44px 60px;
      background: #ffffff 0% 0% no-repeat padding-box;
      box-shadow: 0px 3px 6px #00000029;
      border: 1px solid #f5f5f5;
      border-radius: 8px;
      opacity: 1;
      .row {
        margin-bottom: 10px;
      }
      .form-head {
        font: normal normal bold 30px/37px Roboto;
        font-family: "Roboto", sans-serif;
        letter-spacing: 0px;
        color: #1c66ac;
        text-transform: capitalize;
        text-align: center;
        opacity: 1;
        padding-bottom: 20px;
      }

      .label {
        min-width: 140px;
      }
      .text {
        font-size: 16px;
        color: #42515f;
        opacity: 1;
        font-family: Roboto;
      }
      .error {
        font-size: 14px;
        color: red;
        opacity: 1;
        font-family: Roboto;
        padding-left: 140px;
        margin-bottom: 10px;
      }
      .profile-radio-button {
        display: flex;
        flex-direction: row;
        margin-left: 30%;
      }
      .profile-radio-button > label {
        display: flex;
        flex-direction: row;
        align-items: center;
      }
      .uploadButton {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        cursor: pointer;
      }
      input[type="checkbox"] {
        cursor: pointer;
      }
      .input {
        width: 100%;
        padding: 12px;
        border: 1px solid #ccc;
        border-radius: 4px;
        resize: vertical;
      }
      .buttonParent {
        display: flex;
        margin-bottom: 10px;
        justify-content: space-between;
        margin-top: 20px;
      }
      .button {
        font-size: 20px;
        font-family: Roboto;
        letter-spacing: 0px;
        color: #ffffff;
        opacity: 1;
        background: transparent;
        border: none;
        font-weight: 400;
        min-width: 140px;
        padding: 7px 7px;
        border: 1px solid #707070;
        border-radius: 5px;
        cursor: pointer;
      }
      .submitButton {
        border: none;
        background-color: green;
        margin-right: 30px;
      }
      .resetButton {
        background-color: #42515f;
      }
      .cancelButton {
        /* margin-left: 140px; */
        background-color: rgb(126, 24, 24);
        text-decoration: none;
        text-align: center;
      }

      label {
        padding: 12px 12px 12px 0;
        display: inline-block;
      }
      select {
        padding: 12px;
        border: 1px solid #ccc;
        border-radius: 4px;
        resize: vertical;
        font: normal normal normal 16px Roboto;
        letter-spacing: 0px;
        color: #42515f;
        background-color: transparent;
        margin-right: 10px;
      }
      .profile {
        width: 40px;
        border-radius: 50%;
        margin-left: 12px;
      }
    }
  }
  * {
    box-sizing: border-box;
  }
}

/* flex */
.flex-row-center {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 960px) {
  .payroll-main {
    .content {
      .form {
        width: 85%;
        padding: 44px 20px;
        .form-head {
          font: normal normal bold 25px/30px Roboto;
          font-family: "Roboto", sans-serif;
        }
        .cancelButton {
          margin-left: 0px;
        }
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .payroll-main {
    .content {
      .form {
        margin-top: 10px;
        .form-head {
          font: normal normal bold 22px/27px Roboto;
          font-family: "Roboto", sans-serif;
        }
        .uploadButton {
          justify-content: flex-start;
        }
        .profile-radio-button {
          flex-flow: wrap;
        }
        select {
          margin-right: 5px;
        }
        .submit-reset {
          width: 100%;
          display: flex;
          flex-grow: 1;
          flex-direction: row-reverse;
          justify-content: space-evenly;
          margin-bottom: 20px;
        }
        .submitButton {
          margin: 0;
        }
        .buttonParent {
          flex-direction: column-reverse;
          align-items: center;
        }
      }
    }
  }

  .error {
    padding: 0;
  }

  .row {
    flex-direction: column;
  }
}

